import React from 'react'

export default function Modal_windows_Tailwind_f() {

    let [modal_windows_shadow, setModal_windows_shadow] = React.useState({display: 'none'});
    let [modal_windows_module, setModal_windows_module] = React.useState({display: 'none'});

    function open_modale_windows() {
        setModal_windows_shadow(modal_windows_shadow = {display: ''});
        setModal_windows_module(modal_windows_module = {display: ''});
    };

    function close_modale_windows() {
        setModal_windows_shadow(modal_windows_shadow = {display: 'none'});
        setModal_windows_module(modal_windows_module = {display: 'none'});
    };

    return(
        <div className='w-[600px] h-[300px] font-sans ml-[auto] mr-[auto]'>
            <div className="h-[300px] w-[600px]">
                <div className="fixed left-[0] top-[0] w-[100%] h-[100%] bg-zinc-300 opacity-[0.3] z-[5]]" style={modal_windows_shadow}>
                </div>
                <div className="fixed top-[calc(50%-150px)] left-[calc(50%-200px)] text-center w-[400px] h-[220px] border-solid border-[3px] border-black rounded-[15px] p-[10px] z-[6] bg-white" style={modal_windows_module}>
                    <span className="absolute right-[20px] text-[1.5em] cursor-pointer" onClick={close_modale_windows}>X</span>
                    <b className='text-[1.2em]'>Модальное окно</b>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Debitis dolorem, voluptatibus deleniti nam id sed, ipsam, illum aperiam consequuntur omnis cupiditate perspiciatis voluptate accusamus sequi molestiae deserunt necessitatibus excepturi suscipit?</p>
                    <button className='w-[auto] h-[auto] p-[5px] border-solid border-[1px] border-black rounded-[6px] bg-slate-300' onClick={close_modale_windows}>Закрыть модальное окно</button>
                </div>
                <button className='w-[200px] h-[100px] text-[1.5em] relative top-[calc(50%-50px)] left-[calc(50%-100px)] p-[5px] border-solid border-[1px] border-black rounded-[6px] bg-slate-300' onClick={open_modale_windows}>Открыть модальное окно</button>
            </div>
        </div>
    )
}