import React from 'react' /*6.9k (gzipped: 2.7k)*/

export default function Calculator_f() {

    //Окно вывода расчета;
    let [result_module, setResult_module] = React.useState('ввод данных');

    //Максимальная длина ввода данных;
    let [input_restriction, setInput_restriction] = React.useState(14);

    //Удаление стартового текста;
    let [delete_start_team, setDelete_start_team] = React.useState('yes');
    function Delete_start_f() {
        if (delete_start_team === 'yes') {
            setResult_module(result_module = '');
            setDelete_start_team(delete_start_team = 'no'); 
        }
    }

    //Ввод данных;
    let [last_character, setLast_character] = React.useState('');
    function Click_calculator_f(simvol) {
        Delete_start_f();
        setLast_character(last_character = result_module.split('').reverse()[0]);
        if (result_module.split('').length >= 1 && (simvol === '+' || simvol === '-' || simvol === '*' || simvol === '/') && (last_character === '+' || last_character === '-' || last_character === '*' || last_character === '/')) { Delete_f()}
        if (result_module.split('').length < input_restriction) { setResult_module(result_module = result_module + simvol); console.log(result_module.split('').length)};
    }

    //Удаление данных;
    function Del_f() { setResult_module(result_module = '') };

    let [remove_text, setRemove_text] = React.useState('');
    function Delete_f() {
        Delete_start_f();
        setRemove_text(remove_text = result_module.split(''));
        setRemove_text(remove_text.splice((remove_text.length - 1), 1));
        setResult_module(result_module = remove_text.join(''));
    };

    //Ввывод расчета (+, -, /, *);
    let [eval_string, setEval_string] = React.useState('');
    let [right_bracket, useRight_bracket] = React.useState('');
    let [left_bracket, useLeft_bracket] = React.useState('');
    function Eval_f() {
        useRight_bracket(right_bracket = result_module.split('').filter(RB => RB === ')').length);
        useLeft_bracket(left_bracket = result_module.split('').filter(LB => LB === '(').length);
        if (right_bracket === left_bracket) {
            setDelete_start_team(delete_start_team = 'yes')
            setEval_string(eval_string = result_module);
            // eslint-disable-next-line no-eval
            try { if ((eval(eval_string) - eval(eval_string).toFixed(0)) !== 0) { setResult_module(result_module = eval(eval_string).toFixed(3)) } else { setResult_module(result_module = eval(eval_string)) } } catch (err) { setResult_module(result_module = 'Ошибка') }
        }
    }

    //Квадратный корень 
    function Square_root() {
        // eslint-disable-next-line no-eval
        try { if (((Math.sqrt(eval(result_module))).toFixed(0) - Math.sqrt(eval(result_module))) !== 0) { setResult_module(result_module = String(Math.sqrt(eval(result_module)).toFixed(3))) } else { setResult_module(result_module = String(Math.sqrt(eval(result_module)))) } } catch (err) { setResult_module(result_module = result_module) };
    }

    //Возведение в Квадрат
    // eslint-disable-next-line no-eval
    function Square() {
        // eslint-disable-next-line no-eval
        try { if (((eval(result_module) ** 2).toFixed(0) - eval(result_module) ** 2) !== 0) { setResult_module(result_module = String(((eval(result_module) ** 2)).toFixed(3))) } else { setResult_module(result_module = String((eval(result_module) ** 2))) } } catch (err) { setResult_module(result_module = result_module) };
    }

    //Возведение в Куб
    function Cube() {
        // eslint-disable-next-line no-eval
        try { if ((((eval(result_module)) ** 3).toFixed(0) - (eval(result_module) ** 3)) !== 0) { setResult_module(result_module = String((eval(result_module) ** 3).toFixed(3))) } else { setResult_module(result_module = String((eval(result_module) ** 3))) } } catch (err) { setResult_module(result_module = result_module) };
    }

    //Факториал;
    let [factorial_meaning, setFactorial_meaning] = React.useState(1);
    let [f_n, setF_n] = React.useState(1);
    function Factorial() {
        Eval_f();
        if (result_module === 'Ошибка') { setResult_module(result_module = 'Ошибка') } else {
            // eslint-disable-next-line no-eval
            if ((eval(result_module) - eval(result_module).toFixed(0)) !== 0) { setResult_module(result_module = 'Ошибка') } else {
                // eslint-disable-next-line no-eval
                while (f_n < eval(result_module) + 1) {
                    setFactorial_meaning(factorial_meaning = factorial_meaning * f_n);
                    setF_n(f_n++)
                }
                if (f_n === eval(result_module) + 1) { setF_n(f_n = 1) }
                setResult_module(result_module = factorial_meaning);
                setFactorial_meaning(factorial_meaning = 1);
            }
        }
    }

    return (
        <div className='.body_calculator'>
            <div className="calculator">
                <div className="result_module">{result_module}</div>
                <div className="buttons_module">
                    <button className='button_calculator' onClick={Del_f}>Del.</button>
                    <button className='button_calculator' onClick={Square_root}>&#8730;</button>
                    <button className='button_calculator' onClick={Square}>X&#178;</button>
                    <button className='button_calculator' onClick={Cube}>X&#179;</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('(')}>(</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f(')')}>)</button>
                    <button className='button_calculator' onClick={Factorial}>!</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('*')}>*</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('7')}>7</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('8')}>8</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('9')}>9</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('/')}>/</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('4')}>4</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('5')}>5</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('6')}>6</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('+')}>+</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('1')}>1</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('2')}>2</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('3')}>3</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('-')}>-</button>
                    <button className='button_calculator' onClick={Delete_f}>С</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('0')}>0</button>
                    <button className='button_calculator' onClick={() => Click_calculator_f('.')}>.</button>
                    <button className='button_calculator' onClick={Eval_f}>=</button>
                </div>
            </div>
        </div>
    )
}