import React from 'react'

export default function Modal_windows_f() {

    let [modal_windows_shadow, setModal_windows_shadow] = React.useState({display: 'none'});
    let [modal_windows_module, setModal_windows_module] = React.useState({display: 'none'});

    function open_modale_windows() {
        setModal_windows_shadow(modal_windows_shadow = {display: ''});
        setModal_windows_module(modal_windows_module = {display: ''});
    };

    function close_modale_windows() {
        setModal_windows_shadow(modal_windows_shadow = {display: 'none'});
        setModal_windows_module(modal_windows_module = {display: 'none'});
    };

    return(
        <div className='.body_modal_windows'>
            <div className="modal_windows">
                <div className="modal_windows_shadow" style={modal_windows_shadow}>
                </div>
                <div className="modal_windows_module" style={modal_windows_module}>
                    <span className="cross_close_modal" onClick={close_modale_windows}>X</span>
                    <b className='modal_windows_b'>Модальное окно</b>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Debitis dolorem, voluptatibus deleniti nam id sed, ipsam, illum aperiam consequuntur omnis cupiditate perspiciatis voluptate accusamus sequi molestiae deserunt necessitatibus excepturi suscipit?</p>
                    <button className='modal_windows_button' onClick={close_modale_windows}>Закрыть модальное окно</button>
                </div>
                <button className='modal_windows_button' onClick={open_modale_windows}>Открыть модальное окно</button>
            </div>
        </div>
    )
}