import React, {useEffect, useState} from 'react' /*6.9k (gzipped: 2.7k)*/

export default function Loading_tailwind_f() {

    let [loading_1_level, setLoading_1_level] = React.useState({width: '0px'});
    let [Num_loading_1_level, setNum_loading_1_level] = React.useState(0);
    let [loading_1_percent, setLoading_1_percent] = React.useState('0%');
    let [Num_loading_1_percent, setNum_loading_1_percent] = React.useState(0);

    function loading_f() {
        setLoading_1_level(loading_1_level = {width: `${Num_loading_1_level}px`});
        setLoading_1_percent(loading_1_percent = `${Num_loading_1_percent}%`);
    }

    function loading_click_f() {
        if (Num_loading_1_level !== 900) {
            if (Num_loading_1_percent <= 95){
                setNum_loading_1_level(Num_loading_1_level += 45);
                setNum_loading_1_percent(Num_loading_1_percent += 5);
            } else {
                setNum_loading_1_level(Num_loading_1_level += 900 - Num_loading_1_level);
                setNum_loading_1_percent(Num_loading_1_percent += 100 - Num_loading_1_percent);
            }
        };
        loading_f()
    }

    let [loading_left, setloading_left] = React.useState('no');
    function left_mousedown() {setloading_left(loading_left = 'yes')};
    function left_mouseup() {setloading_left(loading_left = 'no')};
    function left_mouseout() {setloading_left(loading_left = 'no')};
    useEffect(() => {const setInterval_left = setInterval(() => {
        if (Num_loading_1_level !== 0 && loading_left === 'yes') {
            setNum_loading_1_level(Num_loading_1_level -= 9);
            setNum_loading_1_percent(Num_loading_1_percent -= 1);
        };
        loading_f()
    },
        200); return() => clearInterval(setInterval_left)})

    let [loading_right, setLoading_right] = React.useState('no');
    function right_mousedown() {setLoading_right(loading_right = 'yes')};
    function right_mouseup() {setLoading_right(loading_right = 'no')};
    function right_mouseout() {setLoading_right(loading_right = 'no')};
    useEffect(() => {const setInterval_right = setInterval(() => {
        if (Num_loading_1_level !== 900 && loading_right === 'yes') {
            setNum_loading_1_level(Num_loading_1_level += 9);
            setNum_loading_1_percent(Num_loading_1_percent += 1);
        };
        loading_f()
    },
        200); return() => clearInterval(setInterval_right)})

    return (
        <div className='w-[920px] ml-[auto] mr-[auto]'>
            <div className="text-center">
                <span><b>Линейная загрузка</b></span>
                <div className="w-[900px] h-[50px] m-[10px] border-solid border-[1px] border-black">
                    <div className="absolute bg-green-500 w-[0px] h-[50px] z-[2] transition-[300ms]" style={loading_1_level}></div>
                    <div className='relative top-[10px] text-[1.5em] font-bold z-[3]'>{loading_1_percent}</div>
                </div>
                <div className='flex flex-row justify-center gap-[10px]'>
                    <button className='w-[30px] h-[25px] border-solid border-[1px] border-black rounded-[2px] bg-slate-300 active:w-[25px] active:h-[20px] active:mt-[2.5px] active:mb-[2.5px] active:ml-[2.5px] active:mr-[2.5px]' onMouseDown={left_mousedown} onMouseUp={left_mouseup} onMouseOut={left_mouseout}>&#60;</button>
                    <button className='w-[100px] h-[25px] border-solid border-[1px] border-black rounded-[2px] bg-slate-300 active:w-[80px] active:h-[20px] active:mt-[2.5px] active:mb-[2.5px] active:ml-[10px] active:mr-[10px]' onClick={loading_click_f}>Загрузить</button>
                    <button className='w-[30px] h-[25px] border-solid border-[1px] border-black rounded-[2px] bg-slate-300 active:w-[25px] active:h-[20px] active:mt-[2.5px] active:mb-[2.5px] active:ml-[2.5px] active:mr-[2.5px]' onMouseDown={right_mousedown} onMouseUp={right_mouseup} onMouseOut={right_mouseout}>&#62;</button>
                </div>
            </div>
        </div>
    )
}