import React, { useState, useEffect } from "react"
import {count_question} from './questionnaire_questions'

export default function Questionnaire_f() {
    let [count_question_im, setCount_question_im] = React.useState(count_question);
    let n = 0;
    React.useEffect(() => {while (n < count_question_im.length) {
        let question_div_generator = document.getElementById('question_div_generator');
        question_div_generator.insertAdjacentHTML('beforeBegin', `<div id='new_DIV_id_quest_${[n + 1]}'></div>`);
        n++;
    }}, [n]);
    
    let [question_queue, setQuestion_queue] = React.useState(0);
    let [right_answer, setRight_answer] = React.useState(['', 0]);

    let [question_start_class_style, setQuestion_start_class_style] = React.useState({display: 'flex'});
    let [question_class_style, setQuestion_class_style] = React.useState({display: 'none'});
    let [stop_class_style, setStop_class_style] = React.useState({display: 'none'});

    function button_start_id_f() {
        if (question_queue === count_question_im.length) {setQuestion_queue((prev) => prev * 0)};
        if (question_queue < count_question_im.length) {
            setQuestion_start_class_style(question_start_class_style = { display: 'none' });
            setQuestion_class_style(question_class_style = { display: 'flex' });
            let new_DIV_id_ = document.getElementById(`new_DIV_id_quest_${question_queue + 1}`);
            new_DIV_id_.id = `new_DIV_id_quest_0`;
        }
        button_GO_f();
    }

    //Подверждение ответа
    function button_GO_f() {
        let Question_h = document.getElementById('Question_h');
        let Question = document.getElementById('Question');
        let Q_1 = document.getElementById('Q_1');
        let Q_1_Checked = document.getElementById('Q_1_Checked');
        let Q_2 = document.getElementById('Q_2');
        let Q_2_Checked = document.getElementById('Q_2_Checked');
        let Q_3 = document.getElementById('Q_3');
        let Q_3_Checked = document.getElementById('Q_3_Checked');
        let Q_4 = document.getElementById('Q_4');
        let Q_4_Checked = document.getElementById('Q_4_Checked');
        let question_stop = document.getElementById('question_stop');

        if (question_queue !== 0) {
            //При необходимости нужно дабовлять вопросы
            if (Q_1_Checked.checked) { right_answer[0] = Q_1_Checked.value };
            if (Q_2_Checked.checked) { right_answer[0] = String(Q_2_Checked.value) };
            if (Q_3_Checked.checked) { right_answer[0] = String(Q_3_Checked.value) };
            if (Q_4_Checked.checked) { right_answer[0] = String(Q_4_Checked.value) };
            //счет правильных ответов
            if (right_answer[0] === count_question_im[question_queue - 1]['Правильный_ответ']) {
                right_answer[1]++;
            };
        };

        //Добавление текста вопросам
        if (question_queue < count_question_im.length) {
            Question_h.textContent = count_question_im[question_queue]['Заголовок_вопроса'];
            Question.textContent = count_question_im[question_queue]['Вопрос'];
            Q_1.textContent = count_question_im[question_queue]['вопрос_№1'];
            Q_1_Checked.value = count_question_im[question_queue]['вопрос_№1'];
            Q_1_Checked.checked = false;
            Q_2.textContent = count_question_im[question_queue]['вопрос_№2'];
            Q_2_Checked.value = count_question_im[question_queue]['вопрос_№2'];
            Q_2_Checked.checked = false;
            Q_3.textContent = count_question_im[question_queue]['вопрос_№3'];
            Q_3_Checked.value = count_question_im[question_queue]['вопрос_№3'];
            Q_3_Checked.checked = false;
            Q_4.textContent = count_question_im[question_queue]['вопрос_№4'];
            Q_4_Checked.value = count_question_im[question_queue]['вопрос_№4'];
            Q_4_Checked.checked = false;
            setQuestion_queue((prev) => prev + 1);
        }
        //Итог
        if (question_queue === count_question_im.length) {
            setQuestion_class_style(question_class_style = { display: 'none' });
            setStop_class_style(stop_class_style = { display: 'flex' });
            question_stop.textContent = right_answer[1];
        }
    }
    //Кнопка возврата
    function button_stop_f() {
        setStop_class_style(stop_class_style = { display: 'none' });
        setQuestion_start_class_style(question_start_class_style = { display: 'flex' });
        let n = 0;
        while (n < count_question_im.length) {
            let question_class_div_x = document.querySelector(`.question_class > div:nth-child(1) > div:nth-child(${n + 1})`);
            question_class_div_x.id = `new_DIV_id_quest_${n + 1}`;
            right_answer[1] = 0;
            n++
        }
    }

    return (
        <div className="body_question ">
            <div className="question_start_class" style={question_start_class_style}>
                <button id='button_start_id' onClick={button_start_id_f}>Начать тест</button>
            </div>
            <div className="question_class" style={question_class_style}>
                <div>
                    <template id='question_div_generator'></template>
                </div>
                <div>
                    <h3 id="Question_h"></h3>
                    <span className="question" id="Question">

                    </span>
                </div>
                <div className="input_radio_class">
                    <label><input name="group_1" type="radio" id="Q_1_Checked" /><span id="Q_1"></span></label><br />
                    <label><input name="group_1" type="radio" id="Q_2_Checked" /><span id="Q_2"></span></label><br />
                    <label><input name="group_1" type="radio" id="Q_3_Checked" /><span id="Q_3"></span></label><br />
                    <label><input name="group_1" type="radio" id="Q_4_Checked" /><span id="Q_4"></span></label><br />
                </div>
                <div>
                    <button id='button_GO_id' onClick={button_start_id_f}>Далее</button>
                </div>
            </div>
            <div className="question_stop_class" style={stop_class_style}>
                <h3>Итог опроса/теста</h3>
                <div><span>Правильных ответов: </span><span id="question_stop"></span></div>
                <button id='button_stop_id' onClick={button_stop_f}>Завершить опрос/тест</button>
            </div>
        </div>

    )
}