import React, { useEffect } from 'react'

export default function Hover_click_f() {
    let n1 = 0;
    React.useEffect(() => {while (n1 < 1) {
        let hover_click_5 = document.getElementById('hover_click_5');

        let new_div_1 = document.createElement('div');
        new_div_1.id = `style_div_hover_click_1_0`;
        new_div_1.textContent = 'Подсказка (через 2 сек. исчезнет)';
        hover_click_5.append(new_div_1);

        let new_div_2 = document.createElement('div');
        new_div_2.id = `style_div_hover_click_2_0`;
        new_div_1.append(new_div_2);

        n1++;
    }
    });

    let permission = true;
    function hover_click_5_f() {
        if (permission === true) {
            let new_div_1 = document.getElementById('style_div_hover_click_1_0');
            let new_div_2 = document.getElementById('style_div_hover_click_2_0');
            new_div_1.id = `style_div_hover_click_1`;
            new_div_2.id = `style_div_hover_click_2`;
            permission = false;
            setTimeout(setTimeout_hint_f, 2000);
        }
    }

    function setTimeout_hint_f() {
        let new_div_1 = document.getElementById('style_div_hover_click_1');
        let new_div_2 = document.getElementById('style_div_hover_click_2');
        new_div_1.id = `style_div_hover_click_1_0`;
        new_div_2.id = `style_div_hover_click_2_0`;
        permission = true;
    }

    return(
        <div className='body_hover_click'>
            <div className="hover_click">
                <div className="hover_click_block">
                    <b>hover</b>
                    <span>изменение фона блока</span>
                    <div className="hover_click_1"></div>
                    <span>изменение рамки блока</span>
                    <div id="hover_click_2"></div>
                </div>
                <div className="hover_click_block">
                    <b>active (нажмите и не отпускайте)</b>
                    <span>изменение фона блока</span>
                    <div className="hover_click_3"></div>
                    <span>изменение рамки блока</span>
                    <div id="hover_click_4"></div>
                </div>
                <div className="hover_click_block">
                    <b>click</b>
                    <span>сплывающая подсказка</span>
                    <div className="hover_click_5" id='hover_click_5' onClick={hover_click_5_f}></div>
                    <span>нажимная кнопка</span>
                    <div id="hover_click_6"></div>
                </div>
            </div>
        </div>
    )
}